<template>
  <div class="params">
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">

          <el-form-item label="所属公司" size="mini">
            <erpCompanyList v-model="params.company_id" />
          </el-form-item>

          <el-form-item label="所属部门" size="mini">
            <erpDepartment v-model="params.department_id" :searchParams='{company_id:params.company_id}' />
          </el-form-item>

          <el-form-item label="仓库名称" size="mini">
            <el-input v-model="params.warehouse_name" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>

          <el-form-item label="仓库类型" size="mini">
            <el-select clearable v-model="params.warehouse_type_id" placeholder="请选择" filterable @focus="getWarehouseType">
              <el-option :label="item.warehouse_type_name" :value="item.warehouse_type_id" :key="item.warehouse_type_id" v-for="item in warehouseType"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="负责人" size="mini">
            <el-input v-model="params.principal_name" placeholder="请输入" clearable @keyup.enter.native="onSearch(1)"></el-input>
          </el-form-item>

          <el-form-item label="" size="mini">
            <el-checkbox style="padding-left:112px" :false-label="0" :true-label="1" v-model="params.is_disabled" name="type">停用</el-checkbox>
          </el-form-item>

          <el-form-item size="mini" class="search-style search">
            <el-button type="primary" @click="onSearch(1)" size="mini">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>

        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="edit({})" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
      </div>
      <xTable :ajax='{ url: "getmanageList", params: params }' ref='xTable' :tableColumn='tableColumn' />
    </div>
    <editWarehouse v-if='dialogFormVisible' @getData='onSearch' @close='dialogFormVisible = false' :row='row' />
  </div>
</template>

<script>
import { mixins, paramsModule } from "./config/repertorymanage";
import editWarehouse from "./config/editWarehouse";
export default {
  name: "systemInfoSalesRepertorymanage",
  mixins: [mixins],
  data() {
    return {
      authority: {},
      companyLists: [],
      departmentLists: [],
      dialogFormVisible: false,
      warehouseType: [],
      row: null,
      // loading: true,
      params: this.$publicFution.deepCopy(paramsModule),
      multipleSelection: [],
    };
  },
  components: {
    editWarehouse,
  },
  methods: {
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.params = this.$publicFution.deepCopy(paramsModule);
      this.onSearch(1);
    },
    edit(row) {
      if (row.warehouse_id) {
        this.$axMethod
          .getWarehouseInfo({ warehouse_id: row.warehouse_id })
          .then(({ data }) => {
            this.row = data.data;
            this.dialogFormVisible = true;
          });
      } else {
        this.row = {};
        this.dialogFormVisible = true;
      }
    },
    deletes() {
      var deledata = {
        deleID: "warehouse_id",
        deleSelect: this.multipleSelection,
        deleApi: "delManagement",
        list: {
          UrlApi: "getmanageList",
          data: this.TableData,
        },
      };
      this.$common.deletes(deledata, "删除", this);
    },
    getWarehouseType() {
      this.$axMethod
        .getWarehouseTypeList({ company_id: this.params.company_id })
        .then(({ data }) => {
          this.warehouseType = data.data;
        });
    },
    onSearch(page) {
      this.$refs.xTable._getData(page);
    },
  },
};
</script>

