  methods: {
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "合计";
          }
          if (
            ["total_price", "discount_price", "real_num"].includes(
              column.property
            )
          ) {
            if (column.property == 'total_price') {
              this.totalPrice = this.$utils.sum(data, column.property);
              this.countSettlementPrice();
            }
            var num = this.$utils.sum(data, column.property);
            return Number.isInteger(num) ? num : num.toFixed(2)
          }
          return null;
        })
      ];
    }
  }
