<template>
  <div>
    <div class="areaSelectionClass">
      <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
      <br />
      <div class="areaSelection">
        <div v-for="(itme,index) in regionalData" :key="index" class="provinces">
          <el-checkbox :indeterminate="itme.partChecked" v-model="itme.Checked" :label="itme.area_id" :class="{borderStyle:currentIndex===index}" @change="(val)=>{saveSelection(val,index)}">
            <span @click.prevent="requestCity(index)" style="width:80px">{{itme.area_name}} <i class="el-icon-arrow-down"></i></span>
          </el-checkbox>
        </div>
        <div v-if="start" class="cityBounc">
          <el-checkbox v-for="value in regionalData[currentIndex].son" v-model="value.Checked" :label="value.area_id" :key="value.area_id" @change="areaSelection(currentIndex)">
            <span class="inNameThe" :title="value.area_name">{{value.area_name}} </span>
          </el-checkbox>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "areaSelection",
  data() {
    return {
      regionalData: [], //地区数据
      checkAll: null,
      isIndeterminate: false,
      start: false, //弹框开启
      currentIndex: null, //当前点击的下标
    };
  },
  props: {
    selectAllHasId: {
      type: Boolean,
      default: true,
    },
    selectedObject: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  watch: {
    selectedObject() {
      this.initialize();
    },
  },
  methods: {
    hide(event) {
      let btn = document.querySelector(".areaSelectionClass");
      if (btn) {
        if (!btn.contains(event.target)) {
          //按钮.app-download以外的区域
          this.start = false;
        }
      }
    },
    requestCity(index, value = false) {
      if (index == this.currentIndex) {
        if (!value) {
          this.start = !this.start;
        }
      } else {
        this.start = true;
      }
      this.currentIndex = index;
      this.$nextTick(() => {
        if (this.start) {
          let checkboxStyle = document.querySelectorAll(
            ".areaSelection .el-checkbox"
          )[index];
          let cityBounc = document.querySelector(".cityBounc").style;
          cityBounc.left =
            checkboxStyle.offsetLeft > 410
              ? checkboxStyle.offsetLeft - 450 + "px"
              : checkboxStyle.offsetLeft + "px";
          cityBounc.top = checkboxStyle.offsetTop + 32 + "px";
        }
      });
    },
    submitQuery() {
      let obj = {
        is_national_delivery: 0,
        delivery_city: [],
      };
      if (this.checkAll) {
        //如果是全选
        obj.is_national_delivery = 1;
        if (this.selectAllHasId) {
          this.regionalData.forEach((val) => {
            val.son.forEach((itme) => {
              if (itme.Checked) {
                obj.delivery_city.push(itme.area_id);
              }
            });
          });
        }
      } else {
        if (this.isIndeterminate) {
          this.regionalData.forEach((val) => {
            val.son.forEach((itme) => {
              if (itme.Checked) {
                obj.delivery_city.push(itme.area_id);
              }
            });
          });
          //半选
        } else {
          obj.is_national_delivery = 0;
        }
      }
      return obj;
    },
    handleCheckAllChange(val) {
      this.start = false;
      this.isIndeterminate = false;
      if (val) {
        this.regionalData.forEach((val) => {
          this.$set(val, "Checked", true);
          this.$set(val, "partChecked", false);
          val.son.forEach((item) => {
            this.$set(item, "Checked", true);
          });
        });
      } else {
        this.regionalData.forEach((val) => {
          this.$set(val, "Checked", false);
          this.$set(val, "partChecked", false);
          val.son.forEach((item) => {
            this.$set(item, "Checked", false);
          });
        });
      }
      console.log(this.regionalData);
    },
    allmethons() {
      //判断全选
      let areaArry = this.regionalData.filter((item) => {
        return item.Checked === true;
      });
      let partArry = this.regionalData.filter((item) => {
        return item.partChecked === true;
      });
      this.checkAll = areaArry.length == this.regionalData.length; //全选，已勾选的内容等于当前数据
      console.log(this.checkAll);
      if (partArry.length) {
        this.$set(
          //横杆
          this,
          "isIndeterminate",
          true
        );
      } else {
        this.$set(
          //横杆
          this,
          "isIndeterminate",
          areaArry.length > 0 && areaArry.length < this.regionalData.length
        );
      }
    },
    saveSelection(val, index) {
      this.requestCity(index, true);
      //当前点击的省勾选框
      if (val) {
        this.regionalData[index].son.forEach((element) => {
          this.$set(element, "Checked", true);
        });
      } else {
        this.regionalData[index].son.forEach((element) => {
          this.$set(element, "Checked", false);
        });
      }

      this.$set(
        //横杆
        this.regionalData[index],
        "partChecked",
        false
      );
      this.$emit("proChange", val); //当前勾选的省
      this.allmethons();
    },
    areaSelection(index) {
      //当前点击的数据
      let val = this.regionalData[index].son.filter((item) => {
        return item.Checked === true;
      }); //当前点击的市区已勾选内容
      let checkedCount = val.length; //1
      this.regionalData[index].Checked =
        checkedCount == this.regionalData[index].son.length; //全选，已勾选的内容等于当前数据
      this.$set(
        //横杆
        this.regionalData[index],
        "partChecked",
        checkedCount > 0 && checkedCount < this.regionalData[index].son.length
      );
      this.$emit("areaChange", val); //当前勾选的地级市
      this.allmethons();
    },
    initialize() {
      let arr = Object.keys(this.selectedObject);
      if (arr.length) {
        if (this.selectedObject.is_national_delivery) {
          this.handleCheckAllChange(true);
          this.checkAll = true;
        } else {
          let deliveryArry = this.selectedObject.delivery_city.split(",");
          let deliveryNumber = deliveryArry.map(Number);
          this.regionalData.forEach((val, index) => {
            this.$set(val, "Checked", false);
            val.son.forEach((itme) => {
              if (deliveryNumber.includes(itme.area_id)) {
                this.$set(itme, "Checked", true);
                this.areaSelection(index);
              }
            });
          });
        }
      }
    },
  },
  mounted() {
    this.$axios({
      method: "get",
      url: "v2/open/area/getAreaList",
    }).then(({ data }) => {
      if (data.code == 200) {
        this.regionalData = data.data;
        this.initialize();
      }
    });
  },
};
</script>

<style lang="less" scoped>
.areaSelectionClass {
  position: relative;
}
.el-checkbox {
  width: 80px;
  height: 25px;
  line-height: 25px;
  padding: 2.5px 10px;
}
.cityBounc {
  position: absolute;
  width: 550px;
  left: 0px;
  top: 0px;
  z-index: 9999;
  background-color: #ffecd1;
  border: 2px solid #f7ba64;
  //弹框
}
.inNameThe {
  overflow: hidden;
  width: 100px;
  display: inline-block;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.borderStyle {
  // border: 2px solid #f7ba64;
  background-color: #ffecd1;
}

// 新代码
.areaSelection {
  overflow: hidden;
}
.provinces {
  float: left;
}
</style>