<template>
  <div class="params">
    <vxe-modal v-model="isShowDialog" title='仓库维护' @hide="close" id="inReceiptSelectOrder" width="850" :esc-closable='true' destroy-on-close height="800" min-width="600" min-height="100" resize remember storage transfer>
      <div class='warehouseMaintenance' v-loading='loading'>
        <el-form :model="ruleForm" ref="ruleForm" label-width='106px'>
          <p class="title">基础信息</p>
          <el-row>
            <el-col :span='12'>
              <el-form-item label="所属公司" prop="company_id" maxlength="50" :rules="[{ required: true, message: '请选择所属公司', trigger: 'change' }]">
                <erpCompanyList v-model="ruleForm.company_id" :fatherParams='ruleForm' @change="companyChange" :disabled="ruleForm.warehouse_id" />
              </el-form-item>
            </el-col>
            <el-col :span='12'>
              <el-form-item label="所属部门" size="mini">
                <erpDepartment v-model="ruleForm.department_id" :fatherParams='ruleForm' :searchParams='{company_id:ruleForm.company_id}' />
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span='12'>
              <el-form-item label="名称" prop="warehouse_name" maxlength="50" :rules="[{ required: true, message: '请输入编辑属性名称', trigger: 'blur' }]">
                <el-input v-model="ruleForm.warehouse_name" placeholder="请输入"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span='12'>
              <el-form-item label="仓库类型" prop="warehouse_type_id" :rules="rules.warehouse_type_id">
                <el-select clearable v-model="ruleForm.warehouse_type_id" placeholder="请选择" @focus="getWarehouseType">
                  <el-option :label="item.warehouse_type_name" :value="item.warehouse_type_id" :key="item.warehouse_type_id" v-for="item in warehouseType"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row>
            <el-col :span='12'>
              <el-form-item label="所属区域" prop="province_id" :rules="[{ required: true, message: '请选择所属区域', trigger: 'change' }]">
                <getAreaList v-if="defaultArea && defaultArea.length" v-model="cityList" :prop="prop" @changeDefaultArea="changeDefaultArea" :defaultArea="defaultArea" />
              </el-form-item>
            </el-col>
            <el-col :span='12'>
              <el-form-item label="详细地址" prop="address" :rules="[{ required: true, message: '请输入详细地址', trigger: 'blur' }]" size="mini">
                <el-input size="mini" type="text" v-model="ruleForm.address" placeholder="请输入" :maxlength="300"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <p class="title">仓库设置</p>

          <el-row>
            <el-col :span='12'>
              <el-form-item label-width='30px'>
                <el-checkbox v-model="ruleForm.is_default" :true-label='1' :false-label='0'>默认仓库</el-checkbox>
                <el-checkbox v-model="ruleForm.is_market" :true-label='1' :false-label='0'>不参与销售</el-checkbox>
                <el-checkbox v-model="ruleForm.is_disabled" :true-label='1' :false-label='0'>停用</el-checkbox>
              </el-form-item>
            </el-col>
            <el-col :span='12'>
              <el-form-item label="安全库存">
                <el-input v-model="ruleForm.safety_inventory" :maxlength="12" placeholder="请最多输入12个字符" @keyup.native="changeAllocationType"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row v-if="ruleForm.safety_inventory>0">
            <el-col :span='12'>
              <el-form-item label="在库校验" prop="check_stock" :rules="[{ required: true, message: '请选择', trigger: 'blur' }]">
                <el-radio-group v-model="ruleForm.check_stock">
                  <el-radio :label='1'> 单据保存时校验</el-radio>
                  <el-radio :label='2'> 单据审核时校验</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
            <el-col :span='12'>
              <el-form-item label="单据预警" prop="document_warning" :rules="[{ required: true, message: '请选择', trigger: 'blur' }]">
                <el-radio-group v-model="ruleForm.document_warning">
                  <el-radio :label='1'> 强制不通过</el-radio>
                  <el-radio :label='2'> 仅预警提示</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row>
            <el-col :span='12'>
              <el-form-item label='电商仓库'>
                <el-radio v-model="ruleForm.is_ec" :label="0" @change='ruleForm.is_ec_warehouse = 0'> 否</el-radio>
                <el-radio v-model="ruleForm.is_ec" :label="1"> 是</el-radio>
                <el-checkbox v-if='ruleForm.is_ec === 1' v-model="ruleForm.is_ec_warehouse" :true-label='1' :false-label='0'> 默认电商仓库</el-checkbox>
              </el-form-item>
            </el-col>
            <el-col :span='12'>
              <el-form-item label="wms仓库">
                {{ ruleForm.is_wms == '是' ? '是' : '否'}}
              </el-form-item>
            </el-col>
          </el-row>

          <el-row v-if='ruleForm.is_wms === "是"'>
            <el-col :span='24'>
              <el-form-item label='入库推送WMS'>
                <el-radio v-model="ruleForm.instorage_push_opportunity" :label="1"> 采购订单推送</el-radio>
                <el-radio v-model="ruleForm.instorage_push_opportunity" :label="0"> 采购入库单推送</el-radio>
              </el-form-item>
            </el-col>

          </el-row>
          <p class="title">发货区域</p>
          <areaselect ref="area" :selectedObject='selectedObject' :selectAllHasId='false'></areaselect>
          <p class="title">其它信息</p>

          <el-row>
            <el-col :span='12'>
              <el-form-item label="负责人">
                <el-input v-model="ruleForm.principal_name" placeholder="请输入"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span='12'>
              <el-form-item label="负责人电话" prop="phone" :rules="rules.telephone">
                <el-input v-model="ruleForm.phone" placeholder="请输入"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row>
            <el-col :span='12'>
              <el-form-item label="负责人手机号" prop="mobile" :rules="rules.notphone">
                <el-input v-model="ruleForm.mobile" placeholder="请输入"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span='12'>
              <el-form-item label="备注" size="medium">
                <el-input type="text" v-model="ruleForm.remark" maxlength="300" placeholder="请输入"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <div class="dialog-footer">
        <el-button type="primary" @click="submit">确定</el-button>
        <el-button @click="close()">取 消</el-button>
      </div>
    </vxe-modal>
  </div>
</template>

<script>
import { formModule } from "./repertorymanage";
import getAreaList from "common/components/public/getAreaList";
import areaselect from "common/components/plugin/areaSelection/areaSelection.vue";
import { opthionList } from "common/utils/public_function/commonUtils";
export default {
  data() {
    return {
      isShowDialog: true,
      ruleForm: this.$publicFution.deepCopy(formModule),
      defaultArea: [0], //默认的区域id
      warehouseType: [],
      loading: false,
      prop: {
        value: "value",
        children: "cities",
        checkStrictly: true,
      },
      selectedObject: {},
    };
  },
  props: {
    value: null,
    row: {
      type: Object,
      default: () => {},
    },
  },
  methods: {
    childMethos($event) {
      if (this.$refs.area) {
        this.$refs.area.hide($event);
      }
    },
    changeDefaultArea(addressId) {
      this.ruleForm.cityList = addressId.join(",");
      this.ruleForm.province_id = addressId[0];
      this.ruleForm.city_id = addressId[1];
      this.ruleForm.area_id = addressId[2];
      this.$refs.ruleForm.validate();
    },
    companyChange(val) {
      this.ruleForm.warehouse_type_id = "";
      this.ruleForm.department_id = "";
      if (val) {
        this.$axMethod
          .manageInfoWarehouse({ company_id: val.company_id })
          .then((response) => {
            if (response.data.code == 200) {
              if (response.data.data.success) {
                this.ruleForm.is_default = 1;
              } else {
                this.ruleForm.is_default = 0;
              }
            }
          });
      }
    },
    getWarehouseType() {
      this.$axMethod
        .getWarehouseTypeList({ company_id: this.ruleForm.company_id })
        .then(({ data }) => {
          this.warehouseType = data.data;
        });
    },
    submit() {
      let { delivery_city, is_national_delivery } =
        this.$refs.area.submitQuery();
      let areaData = {
        delivery_city: delivery_city.join(","),
        is_national_delivery,
      };
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          let ruleFormParms = Object.assign({}, this.ruleForm, areaData);
          delete ruleFormParms.is_wms;
          var url = this.$api.addmanagement;
          if (this.ruleForm.warehouse_id) {
            url = this.$api.editmanagement;
          }

          this.loading = true;
          this.$axios({
            method: "post",
            url: url,
            data: ruleFormParms,
            params: {
              hideLoding: true,
            },
          })
            .then(({ data }) => {
              if (data.code === 200) {
                this.$message.success(data.msg);
                this.$parent.onSearch();
                this.close();
              }
              this.loading = false;
            })
            .catch(() => {
              this.loading = false;
            });
        }
      });
    },
    changeAllocationType() {
      if (this.ruleForm.safety_inventory > 0) {
        this.$nextTick(function () {
          this.$set(this.ruleForm, "check_stock", 1);
          this.$set(this.ruleForm, "document_warning", 2);
        });
      }
    },
    getData(data) {
      this.ruleForm = data;
      this.selectedObject = {
        delivery_city: data.delivery_city,
        is_national_delivery: data.is_national_delivery,
      };
      opthionList(this, data);
      this.defaultArea = [data.province_id, data.city_id, data.area_id];
      this.warehouseType = [
        {
          warehouse_type_id: data.warehouse_type_id,
          warehouse_type_name: data.warehouse_type_name,
        },
      ];
    },
    close() {
      this.isShowDialog = true;
      this.$emit("close");
    },
  },
  created() {
    if (this.row.warehouse_id) {
      this.getData(this.row);
    }
  },
  components: {
    areaselect,
    getAreaList,
  },
};
</script>

<style lang='less'>
.warehouseMaintenance {
  .title {
    color: #333;
    border-radius: 4px 4px 0 0;
    font-weight: 700;
    background-color: #bcc6d3;
    padding: 0px 5px;
    margin: 5px 0px;
  }
}
</style>
